<template>
	<view class="detail">
		<view class="detail-header">
			<view class="detail-header_face">
				<image :src="fromData.face" mode="aspectFill"></image>
			</view>
			<view class="detail-header_content">
				<view class="detail-header_content-title">
					{{fromData.name}}
				</view>
				<view class="detail-header_content-info">
					<text>{{fromData.time}}</text>
					<text>{{fromData.thumbsup}} 赞</text>
				</view>
			</view>
			<button class="detail-header_fans">关注</button>
		</view>

		<view class="detail-content">
			{{fromData.content}}
			<view class="detail-reward" v-if="fromData.type != '高光'">
				<text>奖励：{{fromData.reward}} 积分</text>
				<button>接受{{fromData.type}}</button>
			</view>
			<view class="detail-comment">
				<view class="detail-comment_title">最新评论</view>
				<view class="detail-comment_content" v-for="item in commentsList" :key="item.index">
					<comments-box :comments="item"></comments-box>
				</view>
			</view>
		</view>
		<view class="detail-bottom">
			<view class="detail-bottom_input" @click="openComment">
				<input placeholder="评论" value=""></input>
				<uni-icons type="compose" size="16"></uni-icons>
			</view>
			<view class="detail-bottom_icons">
				<view class="detail-bottom_icons-box">
					<uni-icons class="detail-bottom_icons-boxicon" type="chat" size="22"></uni-icons>
					<uni-icons class="detail-bottom_icons-boxicon" type="hand-thumbsup" size="22"></uni-icons>
				</view>
			</view>
		</view>
		<uni-popup ref="popup" type="bottom" :maskClick="false">
			<view class="popup-wrap">
				<view class="popup-header">
					<text class="popup-header_item" @click="closeComments">取消</text>
					<text class="popup-header_item" @click="submit">发布</text>
				</view>
				<view class="popup-header_content">
					<textarea class="popup-header_content-textarea" v-model="commentsValue" fixed placeholder="请输入评论内容" maxlength="200"></textarea>
					<view class="popup-header_content-count">{{commentsValue.length}}/200</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				fromData:{},
				commentsValue:'',
				commentsList:[]
			}
		},
		onLoad(query) {
			// console.log(query);
			this.fromData = JSON.parse(query.params)
			this.getCommentList()
		},

		methods: {
			
			openComment(){
				this.$refs.popup.open()
			},
			closeComments(){
				this.$refs.popup.close()
			},
			submit(){
				if(!this.commentsValue){
					uni.showToast({
						title: '评论不能为空',
						icon:"none"
						
					});
				}
				console.log(1);
				this.$refs.popup.close()
			},
			getCommentList(){
					const url = '../../static/json/comments.json'
					this.$http.get(url).then(res => {
						const {data} = res
						var arr = []
						for(var i=0;i<data.length;i++){
							if(data[i].number===this.fromData.number){
								arr.push(data[i])
							}
						}
					this.commentsList = arr
					})
			}
			
		}
	}
</script>

<style lang="scss">
	.detail{
		padding: 15px 0;
		padding-bottom: 54px;
		
		.detail-header{
			display: flex;
			align-items: center;
			padding: 0 15px;
			.detail-header_face{
				flex-shrink: 0;
				width: 50px;
				height: 50px;
				border-radius: 50%;
				overflow: hidden;
				image{
					width: 100%;
					height: 100%;
				}
			}
			.detail-header_content{
				width: 100%;
				padding-left: 10px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				font-size: 12px;
				.detail-header_content-title{
					font-size: 16px;
					padding-bottom: 10px;
					color: #333;
				}
				.detail-header_content-info{
					color: #999;
					text{
						font-size: 14px;
						margin-right: 10px;
					}
				}
			}
			.detail-header_fans{
				flex-shrink: 0;
				font-size: 12px;
				background-color: $mk-base-color;
				color: #fff;
			}
		}
		.detail-content{
			margin-top: 10px;
			padding: 0 15px;
			
			.detail-reward{
				margin-top: 20px;
				display: flex;
				width: 100%;
				align-items: center;
				text{
					font-weight: bold;
					width: 100%;
				}
				button{
					font-size: 12px;
					background-color: red;
					color: #fff;
					border-radius: 10px;
					flex-shrink: 0;
				}
			}
			.detail-comment{
				margin-top: 30px;
				.detail-comment_title{
					padding: 10px 0px;
					font-size: 14px;
					color: #666;
					border-bottom: 1px #f5f5f5 solid;
					.detail-comment_content{
						padding: 0 15px;
						border-top: #eee 1px solid;
						
					}
				}
			}
		}
		.detail-bottom{
			position: fixed;
			left: 0;
			bottom: 0;
			display: flex;
			align-items: center;
			width: 100%;
			height: 44px;
			border-top: 1px #f5f5f5 solid;
			background-color: #fff;
			box-sizing: border-box;
			.detail-bottom_input{
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-left: 10px;
				padding: 0 10px;
				width: 100%;
				height: 30px;
				border: 1px #ddd solid;
				border-radius: 5px;
				text{
					font-size: 14px;
					color: #999;
				}
			}
			.detail-bottom_icons{
				display: flex;
				flex-shrink: 0;
				padding: 0 20px;
				.detail-bottom_icons-box{
					display: flex;
					align-items: center;
					width: 44px;
					.detail-bottom_icons-boxicon{
						padding-right: 10px;
					}
				}
			}
		}
		.popup-wrap{
			background-color: #fff;
			.popup-header{
				display: flex;
				justify-content: space-between;
				font-size: 14px;
				color: #666;
				border-bottom: #f5f5f5 1px solid;
				.popup-header_item{
					height: 50px;
					line-height: 50px;
					padding: 0 15px;
				}
			}
			.popup-header_content{
				width: 100%;
				padding: 15px;
				box-sizing: border-box;
				.popup-header_content-textarea{
					width: 100%;
					color: 200px;
				}
				.popup-header_content-count{
					display: flex;
					justify-content: flex-end;
					font-size: 12px;
					color: #999;
				}
			}
		}
	}
	
</style>
